@charset "utf-8";
// Core variables and mixins
@import "home/base/variables.scss";
@import "home/base/mixins.scss";

// Custom Fonts

 
 @mixin heading-font {
 font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI",
 Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
 "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
 }
 
 @mixin script-font {
 font-family: "Kaushan Script", -apple-system, BlinkMacSystemFont, "Segoe UI",
 Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
 "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
 }
 
 @mixin serif-font {
 font-family: "Droid Serif", -apple-system, BlinkMacSystemFont, "Segoe UI",
 Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
 "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
 }
 
 @mixin body-font {
 font-family: "Roboto Slab", -apple-system, BlinkMacSystemFont, "Segoe UI",
 Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji",
 "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
 }
 


// Custom colors
$primary: #fed136 !default;
$white: #fff !default;
$black: #000 !default;

// Custom Images
$header-image: "../img/header.jpg";
$contact-image: "../img/contact.png";

// Global CSS
@import "home/base/page.scss";

// Components
@import "home/components/buttons.scss";
@import "home/components/navbar.scss";
@import "home/components/client-scroll.scss";

// Layout
@import "home/layout/masthead.scss";
@import "home/layout/services.scss";
@import "home/layout/portfolio.scss";
@import "home/layout/timeline.scss";
@import "home/layout/team.scss";
@import "home/layout/certifications.scss";
@import "home/layout/contact.scss";
@import "home/layout/footer.scss";
