// Styling for the contact section
section#contact {
 background-color: $gray-900;
 background-image: url("#{$contact-image}");
 background-repeat: no-repeat;
 background-position: center;

 .email {
 color: whitesmoke;
 text-align: center;
 }

 .form-process-card {
 border-radius: 10px;
 text-align: center;
 background-color: white;
 padding: 10px;
 margin-bottom: 75px;
 }

 .form-process-card i {
 font-size: xxx-large;
 color: $primary;
 }

 .card1 {
 display: block;
 position: relative;
 background-color: #f2f8f9;
 min-height: 225px;
 border-radius: 4px;
 padding: 32px 24px;
 margin: 12px 12px 75px 12px;
 // text-decoration: none;
 z-index: 0;
 overflow: hidden;
 &:before {
 content: "";
 position: absolute;
 z-index: -1;
 top: -16px;
 right: -16px;
 background: $primary;
 height: 32px;
 width: 32px;
 border-radius: 32px;
 transform: scale(1);
 transform-origin: 50% 50%;
 transition: transform 0.25s ease-out;
 }
 
 &:hover:before {
 transform: scale(30);
 }
 }

 .card1:hover {
 p {
 transition: all 0.3s ease-out;
 color: rgba(255, 255, 255, 0.8);
 }
 h3 {
 transition: all 0.3s ease-out;
 color: #ffffff;
 }
 i {
 color: #ffffff;
 }
 }

 .go-corner {
 display: flex;
 align-items: center;
 justify-content: center;
 position: absolute;
 width: 50px;
 height: 50px;
 overflow: hidden;
 top: 0;
 right: 0;
 background-color: $primary;
 border-radius: 0 4px 0 32px;
 }

 .go-arrow {
 margin-top: -4px;
 margin-right: -4px;
 color: white;
 font-family: courier, sans;
 font-weight: 900;
 font-size: xx-large;
 }

 .form-process-card h5 {
 color: $primary;
 }

 .section-heading {
 color: $white;
 }
 .form-group {
 margin-bottom: 25px;
 input,
 textarea {
 padding: 20px;
 }
 input.form-control {
 height: auto;
 }
 textarea.form-control {
 height: 248px;
 }
 }
 .form-control:focus {
 border-color: $primary;
 box-shadow: none;
 }
 ::-webkit-input-placeholder {
 font-weight: 700;
 color: $gray-400;
 @include heading-font;
 }
 :-moz-placeholder {
 font-weight: 700;
 color: $gray-400;
 @include heading-font;
 }
 ::-moz-placeholder {
 font-weight: 700;
 color: $gray-400;
 @include heading-font;
 }
 :-ms-input-placeholder {
 font-weight: 700;
 color: $gray-400;
 @include heading-font;
 }
}
